








































































































.artist-container {
	text-align: center;
	display: inline-block;
	width: 30%;
	height: auto;
	margin: 1.6%;
	margin-top: 10px;
	background-color: #383838;
	color: #dedede;
	vertical-align: top;
	position: relative;
	font-size: 1.2em;
}

@media only screen and (max-width: 992px) {
	.artist-container {
		width: 47.5%;
		margin-left: 1.25%;
		margin-right: 1.25%;
		font-size: 1.2em;
	}
}

@media only screen and (max-width: 768px) {
	.artist-container {
		width: 75%;
		margin: 5% 12.5%;
		font-size: 1.4em;
	}
}

.artist-image {
	width: 100%;
	overflow: hidden;
	position: relative;
}
.artist-image img {
	width: 100%;
	height: auto;
}

.artist-social {
	margin: 0 auto;
	margin-top: 10px;
	width: 60%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}

.artist-social a {
	color: #dedede;
}

.artist-name {
	font-size: 1.8em;
	line-height: 1;
}

.artist-genre {
	font-size: .8em;
	font-weight: 300;
	font-style: italic;
}

.artist-info {
	width: 100%;
	padding: 15px;
}

.artist-player {
	width: 100%;
	background-color: #1d1717;
}

.artist-player audio {
	width: 100%;
}

.artist-player .now-playing p {
	padding: 10px 15px;
}

.track-images {
	width: 100%;
}
.track-images img {
	float: left;
	width: 33.3%;
	display: inline-block;
	opacity: 0.7;
}
.track-images img:hover {
	opacity: 1;
	cursor: pointer;
}

.icon:hover {
	color: rgb(25,200,70);
}




