








































































form {
  width: 75%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  border: 1px solid #efefef;
}

.search-input {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  padding: 8px;
  font-size: 1.5em;
  margin-right: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.search-input:focus {
  outline: none;
  outline-style: solid;
  outline-width: 1px;
  outline-color: rgb(25,70,20);
}

.submit-button {
  position: relative;
  left: -1px;
  padding: 8px;
  font-size: 1.5em;
  margin: 0;
  background-color: #2b2b2b;
  color: #727272;
  border: 1px solid #727272;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}


.search-input:focus + .submit-button {
  background-color: #1a871c;
  color: #dfdfdf;
}

.grid {
  margin-top: 1%;
}




