








































html {
  height: 100%;
  font-size: 20px;
}

body {
  background-color: #dfdfdf;
  font-size: 14px;
  line-height: 1.4;
  color: #2c3e50;
  font-family: Source Sans Pro, Helvetica, sans-serif;
}

a {
  text-decoration: none;
  color: rgb(25, 200, 70);
  font-size: 1em;
}

nav {
  position: fixed;
  top: 0;
  background-color: #2b2b2b;
  width: 100%;
  padding: 10px;
  z-index: 2;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
nav a {
  font-size: 1.5em;
  margin-left: 20px;
}

